.interactive-scenario-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.interactive-scenario-content {
    background-color: white;
    padding: 20px;
    border-radius: 8px;
    width: 500px;
    position: relative;
}

.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5em;
    cursor: pointer;
}

.scene {
    margin-bottom: 20px;
}

.character {
    font-weight: bold;
}

.choices {
    display: flex;
    flex-direction: column;
    gap: 10px;
}
